.carousel{
    width:100%;
    height:auto;
    /* background-color: aqua; */
    color:black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:2rem 0;
}
.carousel h3{
    margin:2rem 0;
    font-size: 2rem;
    font-weight: 700;
}
.slider{
    width:85%;
    /* background-color: red; */
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.btn1{
    width:25px;
    height:25px;
    padding:20px;
    border-radius: 50%;
    background-color:#2EBF91;
    cursor: pointer;
    margin-right: 1rem;
    box-shadow: 1px 1px 8px black;
}
.btn1 img{
    transform: rotate(180deg);
    width:20px;
    height: auto;
    position: relative;
    bottom:1px;

}
.btn2{
    /* position: absolute;
    right: 0; */
    width:25px;
    height:25px;
    padding:20px;
    background-color: #1AC073;
    border-radius: 50%;
    cursor: pointer;
    margin-left: 1rem;
    box-shadow: 1px 1px 8px black;
}
.btn2 img{
    width:20px;
    height: auto;
    position: relative;
    bottom:3px;
    left: 5px;
}
.request-dish{
    padding: 10px 30px;
    color: white;
    font-weight: 500;
    background-color: #1AC073;
    border: none;
    margin-top: 3rem;
    border-radius: 10px;
    cursor: pointer;
}
.slider2{
    box-shadow: 1px 1px 30px rgb(84, 81, 81);
    border-radius: 10px;
}



 .request-popup{
    width:100%;
    height:100%;
    background-color: rgba(0, 0, 0, 0.416);
    position: absolute;
    top:0;
    z-index: 99;
    display: flex;
    justify-content: center;

 }
 .dish-request-menu{
    background-color: white;
    width:90%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top:3rem;
    border-radius: 10px;
 }
 .dish-request-menu h2{
    margin: 5rem;
 }
 .dish-request-menu label{
    text-align: left;
 }
 .close{
    display: none;
 }
 .input-field{
    width:80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
 }
 .input-field input{
    background-color: #f2f2f2;
    border: none;
    padding: 10px;
    outline: none;
    border-radius: 10px;
 }
 .request-controls{
    margin:3rem 0;
 }

 #cancel{
    background-color: white;
    border: 2px solid #1AC073;
    margin:0 1rem;
    padding: 10px 50px;
    border-radius: 5px;
    text-decoration: none;
 }
 #submit{
    background-color: #1AC073;
    border: none;
    color: white;
    padding:12px 50px;
    margin:0 1rem;
    padding: 10px 50px;
    border-radius: 5px;
    text-decoration: none;
 }

 @media screen and (max-width:1131px) {
   .slider1{
      display:none;
   }
   .slider3{
      display: none;
   }
   .btn1{
      margin-left: 10rem;
   }
   .btn2{
      margin-right: 10rem;
   }
 }
 @media screen and (max-width:413px) {
   .btn1{
      width: 10px;
      height: 10px;
      position:relative;
      left:2rem
   }
   .btn2{
      width: 10px;
      height: 10px;
      position: relative;
      right:2rem;
   }
   .btn1 img{
      position: relative;
      bottom:10px;
      right:7px;

   }
   .btn2 img{
      position: relative;
      bottom:10px;
      /* right:20px; */
      left:-3px
   }
 }